		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待收货'; break;
				case 3:
					stateTip = '已完成'; break;
				case 4:
					stateTip = '售后'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
